import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import {UrlPath} from '../../../../domain/enums/urlpath';
import {StatusPayURequest} from '../../../../domain/models/plans/statuspayurequest.model';

@Injectable()
export class ChangeStatusPayUService implements IRepository<StatusPayURequest> {

  constructor(private http: HttpClient) {
  }

  public create(values : StatusPayURequest): Observable<RootObject> {
    var response = this.http.post<RootObject>(UrlPath.URL_PATH+`/servicebroker/v1/private/plans/pc/changestatuspayu/`,values);
    return response;
  }
}
