import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";

import {StatusPayURequest} from '../../../../domain/models/plans/statuspayurequest.model';
import {ChangeStatusPayUService} from './changestatuspayu.service';

export const ChangeStatusPayUToken = new InjectionToken<IRepository<StatusPayURequest>>('changestatuspayu.service');

export class ChangeStatusPayUProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<StatusPayURequest>> = ChangeStatusPayUToken;
  public deps: any[] = [ChangeStatusPayUService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<StatusPayURequest>[]) => {
    return deps[0]
  };
}
